import PropTypes from 'prop-types';

export default PropTypes.shape({
  id: PropTypes.number,

  quantity: PropTypes.number.isRequired,
  totalPrice: PropTypes.number.isRequired,
  status: PropTypes.string.isRequired,
});

export const formValues = PropTypes.shape({
  quantity: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  totalPrice: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  status: PropTypes.string,
});

export const formTouched = PropTypes.shape({
  quantity: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  totalPrice: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
  status: PropTypes.oneOfType([PropTypes.bool, PropTypes.shape()]),
});

export const formErrors = PropTypes.shape({
  quantity: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  totalPrice: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
  status: PropTypes.oneOfType([PropTypes.string, PropTypes.shape()]),
});
