import { getDefaultOptions, request } from 'api/helpers';

const resource = 'api/loan-products';

export const apiLoanProductGet = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'GET',
  };
  return request(url, options);
};

export const apiLoanProductPost = async (serviceUrl, loanProduct) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'POST',
    body: loanProduct ? JSON.stringify(loanProduct) : null,
  };
  return request(url, options);
};

export const apiLoanProductPut = async (serviceUrl, loanProduct) => {
  const url = `${serviceUrl}/${resource}`;
  const options = {
    ...getDefaultOptions(),
    method: 'PUT',
    body: loanProduct ? JSON.stringify(loanProduct) : null,
  };
  return request(url, options);
};

export const apiLoanProductDelete = async (serviceUrl, id) => {
  const url = `${serviceUrl}/${resource}/${id}`;
  const options = {
    ...getDefaultOptions(),
    method: 'DELETE',
  };
  return request(url, options);
};
